// import action type 
import { CHANGE_SEARCH_FIELD, REQUEST_ROBOTS_PENDING, REQUEST_ROBOTS_SUCCESS, REQUEST_ROBOTS_FAILED } from './constants.js';

// initial state (how state looks like)
const initialStateSearch = {
  searchField: ''
}
//default balue
export const searchRobots = (state = initialStateSearch, action = {}) => {
  console.log(action.type);
  // condition of actions
  switch (action.type) {
    case CHANGE_SEARCH_FIELD:
      // return a new object 
      return Object.assign({}, state, { searchField: action.payload });
    //target , sources ...
    default:
      return state;
  }
}

const initialStateRobots = {
  isPending: false,
  robots: [],
  error: ''
}

export const requestRobots = (state = initialStateRobots, action = {}) => {
  switch (action.type) {
    case REQUEST_ROBOTS_PENDING:
      return Object.assign({}, state, { isPending: true })
    case REQUEST_ROBOTS_SUCCESS:
      return Object.assign({}, state, { robots: action.payload, isPending: false });
    case REQUEST_ROBOTS_FAILED:
      return Object.assign({}, state, { error: action.payload, isPending: true })
    default:
      return state
  }
}